import React from 'react'
import styled from 'styled-components'
import Grid from '@material-ui/core/Grid'
import { CogIcon, RecentIcon, Button, useModal, Text } from '@robustswap-libs/uikit'
import useI18n from 'hooks/useI18n'
import useStake from 'hooks/useStake'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import RecentModal from './components/RecentModal'
import SettingsModal from './components/SettingsModal'
import AddLiquidityModal from './components/AddLiquidityModal'


const Wrapper = styled.div`
  font-size: 16px;
  line-height: 28px;
  font-weight: 500;
  width: 90%;
  max-width: 417px;
  border-radius: 16px;
  margin-left: auto;
  margin-right: auto;
  background-color: #1E215C;
  padding: 24px 24px 32px 24px;
`

const Divider = styled.div`
  background-color: ${({ theme }) => theme.colors.borderColor};
  height: 1px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 24px;
  margin-bottom: 24px;  
  width: 100%;
`

/**
 * Note: currently there should be only 1 active IFO at a time
 */

const Liquidity = () => {

  const { onStake } = useStake(1)

  const TranslateString = useI18n()
  const [onSetting] = useModal(
    <SettingsModal />
  )
  const [onRecent] = useModal(
    <RecentModal />
  )
  const [onConfirm] = useModal(
    <AddLiquidityModal />,
    true
  )
  // const { account } = useWallet()
  const { account } = useWallet()

  return (
    <Wrapper>
      <Grid container spacing={2}>
        <Grid item sm={6} xs={12}>
          <Text style={{ fontSize: 24, fontWeight: 600, marginBottom: 4 }}>
            Your Liquidity
          </Text>
        </Grid>
        <Grid item sm={6} xs={12}>
          <RecentIcon onClick={onRecent} style={{ float: 'right', marginLeft: 10, cursor: 'pointer' }} />
          <CogIcon onClick={onSetting} style={{ float: 'right', cursor: 'pointer' }} />
        </Grid>
        <Text style={{ color: '#FCFCFC', width: '100%' }}>
          Remove liquidity to receive tokens back
        </Text>
        <Divider />
        {!account ? (
          <Text style={{ fontSize: 18, marginTop: 8, marginBottom: 8 }}>
            Connect to a wallet to view your liquidity.
          </Text>
        ) : (
          <>
            <Text style={{ fontSize: 18, marginTop: 8, width: '100%' }}>
              No liquidity found.
            </Text>
            <Text style={{ fontSize: 12, marginTop: 32 }}>
              Don&apos;t see a pool you joined?
            </Text>
            <Button variant="success" style={{ width: 257, marginTop: 12, background: 'transparent', border: '1px solid #5F47EE', color: '#5F47EE', fontWeight: 600 }}>
              Find other LP tokens
            </Button>
          </>
        )}
        <Divider />
      </Grid>
      <Button variant="subtle" style={{ width: '100%', marginTop: 8 }} onClick={onConfirm}>
        {TranslateString(462, 'ADD LIQUIDITY')}
      </Button>
    </Wrapper >
  )
}

export default Liquidity
