import React from 'react'
import { Text, Modal, Card, CardHeader, CardBody, Heading, Button, AddIcon } from '@robustswap-libs/uikit'
import useI18n from 'hooks/useI18n'
import styled from 'styled-components'
import Grid from '@material-ui/core/Grid'
import TokenDiv from './TokenDiv'
import useStyles from '../../../assets/styles'

const InnerCard = styled(Card)`
  border: 1px solid #524b63;
  border-radius: 12px;
`

interface AddLiquidityModalProps {
  onDismiss?: () => void
}

const AddLiquidityModal: React.FC<AddLiquidityModalProps> = ({ onDismiss }) => {
  const classes = useStyles.chart()
  const TranslateString = useI18n()

  return (
    <Modal title="Add Liquidity" onDismiss={onDismiss}>
      <Text style={{ color: '#A0B9FB', marginTop: 16 }}>Add liquidity to receive LP tokens</Text>
      <TokenDiv max prefix="Input" coin="BNB" />
      <AddIcon style={{ marginLeft: 'auto', marginRight: 'auto' }} />
      <TokenDiv prefix="Input" coin="ETH" />
      <InnerCard>
        <CardHeader>
          <Heading as="h3" size="md" style={{ fontSize: 12, lineHeight: '14px', textAlign: 'left' }}>
            {TranslateString(594, 'Price and pool share')}
          </Heading>
        </CardHeader>
        <CardBody>
          <Grid container spacing={2} style={{ textAlign: 'center' }}>
            <Grid item sm={4} xs={12}>
              <Text>0.12</Text>
              <Text style={{ fontSize: 12 }}>ETH per BNB</Text>
            </Grid>
            <Grid item sm={4} xs={12}>
              <Text>10.5897</Text>
              <Text style={{ fontSize: 12 }}>BNB per ETH</Text>
            </Grid>
            <Grid item sm={4} xs={12}>
              <Text>0.01%</Text>
              <Text style={{ fontSize: 12 }}>Share of Pool</Text>
            </Grid>
          </Grid>
        </CardBody>
      </InnerCard>
      <Button variant="subtle" style={{ width: '100%', marginTop: 24 }} onClick={onDismiss}>
        {TranslateString(462, 'SUPPLY')}
      </Button>
    </Modal >
  )
}

export default AddLiquidityModal